import React from "react";
import { Link } from "react-scroll";
import "./Navbar.scss";

const Navbar = () => {
  return (
    <>
      <nav className="navbar sticky-top navbar-expand-lg navbar-dark">
        <div className="container-fluid">
          <div className="nav-header"><a
            className="navbar-brand"
            href="https://www.youtube.com/watch?v=dQw4w9WgXcQ"
            target="_blank"
            rel="noreferrer"
          >
            Nischay Gupta
          </a>
          <ul class="nav-socials justify-content-right">
                <li><a href="https://github.com/NickDL29" target="_blank" rel="noreferrer"><i class="fa fa-github"></i></a></li>
                <li><a href="https://www.instagram.com/nickdl42/" target="_blank" rel="noreferrer"><i class="fa fa-instagram"></i></a></li>
                <li><a href="https://www.linkedin.com/in/nischay29/" target="_blank" rel="noreferrer"><i class="fa fa-linkedin-square"></i></a></li>
                <li><a href="#"><i class="fa fa-twitter"></i></a></li>
            </ul>
          </div>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="offcanvas"
            data-bs-target="#offcanvasDarkNavbar"
            aria-controls="offcanvasDarkNavbar"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div
            className="offcanvas offcanvas-end text-bg-dark"
            tabindex="-1"
            id="offcanvasDarkNavbar"
            aria-labelledby="offcanvasDarkNavbarLabel"
          >
            <div className="offcanvas-header">
              <h5 className="offcanvas-title" id="offcanvasDarkNavbarLabel">
                Nick
              </h5>
              <button
                type="button"
                className="btn-close btn-close-white"
                data-bs-dismiss="offcanvas"
                aria-label="Close"
              ></button>
            </div>
            <div className="offcanvas-body">
              <ul className="navbar-nav justify-content-center flex-grow-1 ">
                <li className="nav-item">
                  <Link
                    className="nav-link active"
                    aria-current="page"
                    to="Hero"
                    smooth={true}
                    offset={0}
                    duration={100}
                    data-bs-dismiss="offcanvas"
                  >
                    Home
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className="nav-link active"
                    aria-current="page"
                    to="About"
                    smooth={true}
                    offset={-50}
                    duration={100}
                    data-bs-dismiss="offcanvas"
                  >
                    About
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className="nav-link active"
                    aria-current="page"
                    to="Projects"
                    smooth={true}
                    offset={-50}
                    duration={100}
                    data-bs-dismiss="offcanvas"
                  >
                    Projects
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className="nav-link active"
                    aria-current="page"
                    to="Contact"
                    smooth={true}
                    offset={50}
                    duration={100}
                    data-bs-dismiss="offcanvas"
                  >
                    Contact
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
