import plant from '../../assets/plant.png';
import storage from '../../assets/storage.png';
import table from '../../assets/table.png';
import lamp from '../../assets/lamp.png';
import monitor from '../../assets/monitor.png';

const Hero = () => {
  return (
    <>
      {/* <div className="hero-container">
        <div className="jumbotron jumbotron-fluid">
          <div className="container-main">
          <div className="hero-1 text-white">
            <h1 className="display-4">Hi..!!<br/> This is Nick..!!</h1>
          </div>
          <div className="setup">
            <img className="setupimg" src="./setup.jpg" alt="" />
          </div>
          </div>
        </div>
      </div> */}
      <div className="maincontainer" id="Hero">
        <div className="textcontainer">
        <motion.div
          initial={{ top: "0px" }}
          whileInView={{ top: "10px" }}
        >
          <h1 id="greet">
            Hi..!! <br /> This Is Nick.
          </h1>
        </motion.div>
        <div className="static-txt">I'm a</div>
          <ul className="dynamic-txts">
            <li><span>Developer</span></li>
            <li><span>Designer</span></li>
            <li><span>Freelancer</span></li>
            <li><span>Student</span></li>
          </ul>
        </div>
        <div className="setupcontainer">
    <div className="container">
      <img src={books} className="setup-obj" data-value="-1" alt=""/>
      <img src={carpet} className="setup-obj" data-value="-1" alt=""/>
      <img src={table} className="setup-obj" data-value="-1" alt=""/>
      <img src={monitor} className="setup-obj" data-value="-2" alt=""/>
      <img src={cpu} className="setup-obj" data-value="1" alt=""/>
      <img src={chair} className="setup-obj" data-value="4" alt=""/>
      <img src={lamp} className="setup-obj" data-value="1" alt=""/>
      <img src={plant} className="setup-obj" data-value="-1" alt=""/>
      <img src={storage} className="setup-obj" data-value="-1" alt=""/>
    </div>
  </div>
      </div>
    </>
  );
};

export default Hero;

document.addEventListener("mousemove", parallax);
    function parallax(e){
      document.querySelectorAll(".setup-obj").forEach(function(move){

        var moving_value = move.getAttribute("data-value");
        var x = (e.clientX * moving_value) / 250;
        var y = (e.clientY * moving_value) / 250;

        move.style.transform = "translateX(" + x + "px) translateY(" + y + "px)";
      });
    }


